
.rjcv-card {
  background-image: -webkit-gradient(linear, left top, left bottom, color-stop(50%, #ddd), color-stop(50%, transparent));
  background-image: -webkit-linear-gradient(top, #ddd 50%, transparent 50%);
  background-image: -o-linear-gradient(top, #ddd 50%, transparent 50%);
  background-image: linear-gradient(to bottom, #ddd 50%, transparent 50%);
  background-position: left top;
  background-size: 100% 1px;
  background-repeat: no-repeat;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -webkit-align-items: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 0.2rem;
  text-decoration: none;
}
.rjcv-card .rjcv-card-image {
    width: 2.5rem;
    height: 1.5rem;
}
.rjcv-card .rjcv-card-image img {
      width: 100%;
      height: 100%;
}
.rjcv-card .rjcv-card-detail {
    position: relative;
    z-index: 1;
    -webkit-box-flex: 1;
    -webkit-flex: 1;
        -ms-flex: 1;
            flex: 1;
    width: 4rem;
    height: 1.5rem;
    margin-left: 0.2rem;
    word-break: break-all;
}
.rjcv-card .rjcv-card-detail .rjcv-card-title {
      overflow: hidden;
      font-size: 0.28rem;
      -o-text-overflow: ellipsis;
         text-overflow: ellipsis;
      max-height: 0.6rem;
      line-height: 0.3rem;
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
      color: #121212;
}
.rjcv-card .rjcv-card-detail .rjcv-card-desc {
      overflow: hidden;
      font-size: 0.24rem;
      -o-text-overflow: ellipsis;
         text-overflow: ellipsis;
      max-height: 0.3rem;
      line-height: 0.3rem;
      white-space: nowrap;
      margin-top: 0.1rem;
      color: #666;
}
.rjcv-card .rjcv-card-detail .rjcv-card-price {
      overflow: hidden;
      font-size: 0.3rem;
      -o-text-overflow: ellipsis;
         text-overflow: ellipsis;
      max-height: 0.3rem;
      line-height: 0.3rem;
      white-space: nowrap;
      position: absolute;
      bottom: 0;
      z-index: 2;
      width: 100%;
      font-size: 0.3rem;
      color: #ff397e;
}

.rjcv-image {
  max-width: 100%;
  vertical-align: top;
  border: 0;
}
